* {
    box-sizing: border-box;
    margin: 0px;
} 

body {
    color: #2D334A;
    display: grid;
    grid-template-areas: 
    "head"
    "main"
    "footer";
    //grid-template-rows: ;
}

header {
    grid-area: head;
    background: #0C9463;
    height: 200px;
    
}

main {
    grid-area: main;
    background: #FBE3B9;
    height: 100px;
}

footer {
    grid-area: footer;
    background: #FAB696;
};

